<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title" v-if="orderInfo">
          <span> {{ orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart" v-if="orderInfo">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;
               &nbsp; .
               &nbsp; &nbsp;
                .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
               {{orderInfo.paymentType}}
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <li class="cart__order" v-for="item in orderInfo.basket.items" :key="item.id">
              <h3>{{ item.productOffer.title}}</h3>
              <b>{{item.productOffer.price}}  x {{item.quantity}}</b>
              <span>: {{item.productOffer.id}}</span>
            </li>
          </ul>

          <div class="cart__total">
            <p>: <b>{{orderInfo.deliveryType.price}} </b></p>
            <p>:
                <b>{{cartTotalCount}}</b>    <b>{{priceWithDelivery}} </b>
            </p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>

export default {
  name: 'OrderInfoPage',
  data() {
    return {
      orderInfo: null,
    };
  },
  computed: {
    priceWithDelivery() {
      return +this.orderInfo.deliveryType.price + +this.orderInfo.totalPrice;
    },
    cartTotalCount() {
      return this.orderInfo.basket.items
        .reduce((acc, item) => (item.quantity) + acc, 0);
    },
  },
  created() {
    if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
      this.orderInfo = this.$store.state.orderInfo;
      return;
    }
    this.$store.dispatch('loadOrderInfo',
      this.$route.params.id).then(() => { this.orderInfo = this.$store.state.orderInfo; });
  },
};
</script>

